<template>
  <!-- Button trigger modal -->
  <button type="button" class="btn btn-warning btn-sm" data-toggle="modal"
          data-target="#myModal">
  </button>

  <!-- Modal -->
  <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true"></span>
          </button>
          <h4 class="modal-title" id="myModalLabel">{{order.order_no}}</h4>
        </div>
        <div class="modal-body order-tables">
          <table class="table table-striped">
            <thead>
            <tr>
              <th></th>
              <th></th>
            </tr>
            </thead>
            <tbody>
            <tr style="text-align: left;" v-for="sku in order.skus">
              <td>
                <span>{{sku.name}}</span>
              </td>
              <td>{{sku.quantity}}</td>
            </tr>
            </tbody>
          </table>
          <div class="form-group">
            <div class="row">
              <div class="col-sm-2"><label for="" class="form-label"></label></div>
              <div class="col-sm-4">
                <select name="" id="" class="form-control" v-model="info.company">
                  <option value="default" disabled="disabled"></option>
                  <option value=""></option>
                  <option value="DHL">DHL</option>
                  <option value="EMS">EMS</option>
                  <option value="Fedex">Fedex</option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value="TNT">TNT</option>
                  <option value="UPS">UPS</option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
            </div>
          </div>
          <div class="form-group">
            <div class="row">
              <div class="col-sm-2"><label for="" class="form-label"></label></div>
              <div class="col-sm-4">
                <input type="text" class="form-control" v-model="info.post_no">
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-default" data-dismiss="modal">
            
          </button>
          <button @click.prevent="submit" type="button" class="btn btn-primary" :disabled="!valid"></button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  export default {
    name: 'Express',
    props: ['order'],
    computed: {
      valid () {
        return this.info.company && this.info.post_no.length > 0
      }
    },
    data () {
      return {
        info: {
          company: '',
          post_no: ''
        }
      }
    },
    methods: {
      submit () {
        Vue.http.put('/admin/mall/orders/' + this.order.order_no, this.info).then(
          function (data) {
            window.alert('')
            window.location.reload()
          },
          function (error) {
            console.error(error)
          }
        )
      }
    }
  }
</script>



